<div class="w-100 d-flex justify-content-center"><div class="d-flex justify-content-center flex-grow-1 team-wrap" v-for="team in RoleArray" :style="[{'background-color': team.color}]"><div class="card-wrap position-relative" v-for="item in team.roles" :class="item.color" :style="[{'background-image': `url(./${item.src})`},{'width': Math.round(cardWidth)+'px'}]"><div class="card-panel d-flex justify-content-center"><p class="ver-text card-title m-0">{{ item.label }}</p><p class="ver-text card-desc m-0" :class="item.color">{{ item.desc }}</p></div></div></div></div>